.errormain {
  height: 77vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 30px;
}
.errortext {
  display: flex;
  align-content: center;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  text-decoration: underline;
}
.errorbutton {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  width: 150px;
  height: 75px;
  text-decoration: none;
  color: black;
  border-radius: 10px;
  text-align: center;
  border: 1px solid black;
}
@media (max-width: 1366px) {
  .errormain {
    height: 488px;
  }
}
@media (max-width: 415px) {
  .errormain {
    height: 600px;
  }
}
