.leagueCard {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 415px) {
  .LeagueCard {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.leagueCard img {
  background-color: rgb(255, 255, 255);
  border: 2px solid black;
  border-radius: 30px;
  height: 130px;
  width: 130px;
}
.cards {
  color: white;
  font-size: 25px;
}

@media (max-width: 1920px) {
  .cards {
    font-size: 20px;
  }
  .LeagueCard img {
    height: 110px;
    width: 110px;
  }
}
@media (max-width: 1366px) {
  .cards {
    font-size: 18px;
    text-align: center;
  }
}
@media (max-width: 415px) {
  .cards {
    font-size: 14px;
    text-align: center;
  }
  .LeagueCard {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.leagueCardButton {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 7px;
  text-decoration: none;
}
.link {
  text-decoration: none;
}
